main {
  max-width: 1100px;
  flex-flow: column wrap; }


h1 {
  padding: 35px 0 35px 0;
  font: 24px 'Roboto', sans-serif;
  color: #555555;
  margin: 0;
  font-weight: 600; }

p {
  font: 300 16px 'Roboto', sans-serif;
  line-height: 1.5;
  margin: 10px 0; }

h3 {
  padding: 10px 0 10px 0;
  font: 20px 'Roboto', sans-serif;
  color: #e30000;
  margin: 0;
  border-bottom: 1px solid #e30000; }

li {
  list-style: disc;
  padding-bottom: 10px;
  margin-left: 45px; }



