@import "../common/media";

header {
  background: #fff;
  width: 100%;
  box-shadow: 0px 1px 12px 0px #d2d2d2; }

.actions-wrap {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  align-self: stretch;

  .account, .find, .burger {
    width: 45px;
    height: 45px;
    padding: 10px;
    fill: #4e4e4e; }

  #burger-label {
    align-self: stretch;
    display: flex;
    align-items: center;

    span {
      display: flex;
      position: relative;
      align-items: center;
      justify-content: center;
      top: 1px;
      left: 1px;
      background-color: $dark_grey;
      font-size: 21px;
      cursor: pointer;
      z-index: 5;
      width: 30px;
      height: 4px;
      margin: 20px 10px;

      &:before {
        content: '';
        position: absolute;
        top: 10px;
        left: 0px;
        width: 100%;
        height: 4px;
        background-color: $dark_grey; }

      &:after {
        content: '';
        position: absolute;
        top: -10px;
        left: 0px;
        width: 100%;
        height: 4px;
        background-color: $dark_grey; } } }

  #burger-button:checked {
    & ~ #burger-menu {
      left: 0; } } }

#burger-menu {
  display: block;
  position: absolute;
  top: calc(100% + 3px);
  left: -100vw;
  background: #4e4e4e;
  padding-bottom: 65px;
  transition: left .3s;
  z-index: 4;

  .wrap {
    margin: 50px 20px 0 20px;
    width: 360px;

    hr {
      padding: 0;
      border: none;
      border-bottom: 1px solid #828282;
      box-shadow: 0px 3px 6px 1px #828282;
      width: 100%; }

    a {
      display: flex;
      color: #fff;
      padding: 10px 0 10px 34px;
      width: 360px;
      opacity: 1;
      text-transform: uppercase;

      &:hover {
        background: rgb(255, 255, 255);
        background: radial-gradient(circle, rgba(255, 255, 255, 0.1339031339031339) 0%, rgba(255, 252, 252, 0) 85%); } } } }

.h-upper {

  .logo-wrap {

    .logo-vitex {

      @media screen and (max-width: $screen_md) {
        display: none; } }

    .logo-square {

      @media screen and (max-width: $screen_md) {
        display: flex;
        width: 50px; } }


    >svg {
      width: 250px;
      height: 40px; }

    span {
      @media screen and (max-width: $screen_md) {
        font-size: xx-small; } } } }





